<template>
  <div>
    <Header></Header>
    <Slidebar @child_event="pslidefunc"></Slidebar>
    <div class="o-wrap" v-bind:class="{hide_sidebar: !slide}">
      <div class="page-wrap" id="container" data-page="/">
        <div class="page-player" _ouj_init="true">
          <div class="light-off-wrap"></div>
          <div class="mod-primary">
            <div class="row row-info clearfix">
              <div class="title">
                <div class="text">
                  <p>
                    <a :title="urVideo.title">
                      <p class="name">
                        {{urVideo.ctitle}}
