                    </a>
                  </p>
                  <p class="info">
                    <i class="icon-play"></i>
                    <span>
                      ({{urVideo.view_times}})次观看</span>
                                    
                  </p>
                </div>
                <ul class="list-ctrl clearfix">
                  <li class="ctrl-item " data-playfavorite="" data-cid="81173" @click="like()">
                    <span>
                    <i class="fa fa-heart-o" aria-hidden="true" v-if="isClick == 0"></i>
                    <i class="fa fa-heart" aria-hidden="true" v-if="isClick == 1"></i>
                      {{urVideo.like_times}}
                    </span>
                  </li>
                  <li class="ctrl-item show-code" style="display:none;">
                    <div class="show-line"></div>
                    <a class="show-content" href="/dl/" data-href="">
                      <i class="icon-phone"></i>
                      <div class="content-text">
                        <p>手机看</p>
                      </div>
                    </a>
                    <div class="code-box expand-animate">
                      <div class="code-wrap">
                        <img src="/static/img/appqrcode.png?880dae5fe7d42157">
                      </div>
                    </div>
                  </li>
                </ul>
              </div>
              <div class="upz">
                <div class="upz-inner">
                  <a class="avatar-wrap" title="兔英菲涅特" data-href="" href="#">
                    <img src="#" class="avatar">
                  </a>
                  <!--
                                <div class="text">
                                    <p class="name">
                                        <a href="//yun.duowan.com/up_550/" data-href="">
                                            兔英菲涅特</a>
                                        <i class="icon-v g-upz-mark" title="作者已入驻云玩游戏"></i>
                                    </p>
                                    <p class="info">
                                        90个作品 / 14.3万播放                        
                                    </p>
                                </div>
                                <a class="btn-follow " data-upfollow="" data-upid="550">
                                    关注
                                </a>
                                -->
                </div>
              </div>
            </div>
            <div class="row row-player clearfix">
              <div class="video-content">
                <div preload="auto" class="video-js vjs-controls-enabled vjs-workinghover vjs-v6 vjs-playing vjs-has-started vjs-user-active video-9012899-dimensions" data-part="1" data-articleid="237263" data-cid="81173" id="video-9012899" poster="" lang="zh-cn" role="region" aria-label="Video Player" tabindex="1">
                  <video controls muted poster="" id="video-9012899_html5_api" data-cid="81173" data-articleid="237263" data-part="1" class="vjs-tech" preload="auto" tabindex="-1" :src="avatar">
                    <!-- <source src="" type="video/mp4"> -->
                    <p>您的浏览器不支持播放视频</p>
                  </video>
                </div>
              </div>
              <div class="comment">
                <div class="hd">讨论区<i>（2）</i></div>
                <div class="input-wrap">
                  <div class="input-inner">
                    <div class="input-content">
                      <input class="comment-cont" type="text" placeholder="">
                      <a class="btn-send pointer disabled" data-cid="83380" data-articleid="237157" data-upid="577" data-uname="">发送</a>
                      <p style="display: block;"><a href="javascirpt:;" data-login="">登录</a> 和大家一起讨论吧~</p>
                    </div>
                  </div>
                </div>
                <div class="comment-wrap" data-timeline="2842" data-cid="83380" style="height: 568px;">
                  <ul>
                    <li>
                      <p class="username"><em>来_yy</em>(12-04 15:50:39)</p>
                      <p>
                        ...<a data-commentid="2842" data-uid="4026121" data-username="来_yy" class="btn-reply">回复</a>
                      </p>
                    </li>
                    <li>
                      <p class="username"><em>千里君</em>(12-04 17:46:01)</p>
                      <p>
                        ？？<a data-commentid="2844" data-uid="4026116" data-username="千里君" class="btn-reply">回复</a>
                      </p>
                    </li>
                  </ul>
                </div>
              </div>
            </div>
          </div>
          <div class="mod-video   full">
          </div>
          <div class="mod-recomm">
            <div class="video">
              <!--该主播的其他视频-->
              <!--为你推荐-->
              <div class="g-mod-title clearfix mt-30">
                <h2 class="title-recommend">为你推荐</h2>
              </div>
              <ul class="g-comlist ext-col4">
                <li class="g-col-item" v-for="site in curPageList">
                  <router-link :to="'/detail'+site.id" class="item-title" :title="site.title">
                      <img :src="site.vimg">
                      <div class="mask"></div>
                      <div class="icon-play"></div>
                  </router-link>
                  <router-link :to="'/detail'+site.id" class="item-title" :title="site.title">
                      {{site.title}}
                  </router-link>
                </li>
              </ul>
            </div>
            <!--排行榜-->
            <div class="rank">
              <div class="g-mod-title clearfix">
                <h2 class="title-rank">排行榜</h2>
              </div>
              <ul>
                
                <li v-for="site in topPageList">
                  <router-link :to="'/detail'+site.id">
                      {{site.title}}
                  </router-link>
                </li>
              </ul>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
import Header from './Header.vue'
import Slidebar from './Slidebar.vue'
//import avatar from '@/assets/setVideoUrlLow.mp4'
export default {
  name: 'Index',
  components: {
    Header,
    Slidebar
  },
  data() {
    return {
      url: this.GLOBAL.serviceUrl + '?t=1',
      tagUrl: this.GLOBAL.serviceUrl + '?t=3',
      relateUrl: this.GLOBAL.serviceUrl + '?t=4',
      likesUrl: this.GLOBAL.serviceUrl + '?t=5',
      viewUrl: this.GLOBAL.serviceUrl + '?t=6',
      topUrl: this.GLOBAL.serviceUrl + '?t=7',
      tableData: [],
      total: 0,
      cur_page: 1,
      pageSize: 0,
      pageList: [],
      tData: [],
      avatar: "http://www.qcxxx.com/setVideoUrlLow.mp4",
      slide: 1,
      curHeight: 'height:272.767px',
      urVideo: {},
      user_history: [],
      cur_history: '',
      isClick:0,
      firstLick:0,
      myPlayer:'',
      topPageList:[]

    }
  },
  created() {
    this.getData();
    this.getRelatedData()
    this.getViewData()
    this.getTopData()
  },
  mounted() {
    //this.getData();
  },
  watch: {
    '$route'(to, from) {
      this.getData();
      this.getRelatedData()
      this.getViewData()
    }
  },
  computed: {
    data() {
      return this.tableData
    },
    curPageList() {
      return this.pageList
    },
    tagData() {
      return this.tData
    },
    "msg"() {
      return "asdff"
    }
  },
  methods: {
    like() {
        if(this.isClick==0){
          this.isClick=1
          this.urVideo.like_times +=1
          if(this.firstLick == 0){
              this.firstLick = 1
              this.addLikes()
          }
        }else{
          this.isClick=0
          this.urVideo.like_times -=1
        }
    },
    addLikes() {
        var a = this.commonfunc._getRandom()
        var nonce = parseInt(a)
        var token = this.commonfunc._gtokene(nonce)
        var that = this
        this.no = this.$route.params.no
        var that = this
        this.axios.post(this.likesUrl, this.qs.stringify({ no: this.no}), {
          headers: { 'Content-Type': 'application/x-www-form-urlencoded', 'nonce': nonce, 'token': token }
        }).then(function(res) {
     
        }).catch(function(err) {

        })
    },
    setHistoryData() {
      if (this.cur_history != undefined || this.cur_history != '') {
        this.commonfunc.saveSearch(this.cur_history,1);
      }
    },
    iniVideo() {
      this.myPlayer = this.$video(document.getElementById('video-9012899_html5_api'), {
        //确定播放器是否具有用户可以与之交互的控件。没有控件，启动视频播放的唯一方法是使用autoplay属性或通过Player API。
        controls: true,
        //自动播放属性,muted:静音播放
        autoplay: false,
        //建议浏览器是否应在<video>加载元素后立即开始下载视频数据。
        preload: "auto",
        //设置视频播放器的显示宽度（以像素为单位）
        // width: "800px",
        //设置视频播放器的显示高度（以像素为单位）
        // height: "400px",
        controlBar: {
          playToggle: true
        }
      });

    },
    playVideo(url) {
      let vdo = document.getElementById("video-9012899_html5_api")
      vdo.src = url;
      vdo.play();
    },
    getType(_type = 0) {
      if (_type != this.ltype) {
        this.tableData = []
        this.more = this.cur_page = 1
      } else {
        return false
      }
      this.ltype = _type
      this.getData();
    },
    loadMore() {
      this.cur_page += 1
      if (this.$route.params.k !== undefined) {
        this.getKData();
      } else {
        this.getData();
      }

    },
    pslidefunc(data) {
      this.slide = parseInt(data)
      if (!data) {
        this.curHeight = 'height:287.403px'
      } else {
        this.curHeight = 'height:272.767px;'
      }
      console.log(this.curHeight)
    },
    refresh() {
      this.getData();
    },
    curTitle(val) {
      let obj = {};

      obj = this.role_list.find((item) => {

        return item.id === val;

      });
      this.selected_title = obj.title
    },
    // 分页导航
    handleCurrentChange(val) {
      this.cur_page = val;
      this.getData();
    },
    // 获取 easy-mock 的模拟数据
    getData() {
      if (this.$route.params.no != '' || this.$route.params.no !== undefined) {
        var a = this.commonfunc._getRandom()
        var nonce = parseInt(a)
        var token = this.commonfunc._gtokene(nonce)
        var that = this
        this.no = this.$route.params.no
        var that = this
        this.axios.post(this.url, this.qs.stringify({ no: this.no }), {
          headers: { 'Content-Type': 'application/x-www-form-urlencoded', 'nonce': nonce, 'token': token }
        }).then(function(res) {
          if (parseInt(res.data.message) == 0) {
            //that.playVideo(res.data.data.lowUrl)
            that.iniVideo()
            that.myPlayer.src(res.data.data.lowUrl)
            /*
            that.myPlayer.load()
            that.myPlayer.play()
            */
            that.myPlayer.play()
            that.urVideo = res.data.data
            that.cur_history = JSON.stringify(res.data.data)
            that.setHistoryData()
            //that.urVideo.view_times = 27505
            if (that.urVideo.view_times === undefined || that.urVideo.view_times == null) {
              that.urVideo.view_times = 27506
            }
            if (that.urVideo.like_times === undefined || that.urVideo.like_times == null) {
              that.urVideo.like_times = 27936
            }
            //that.avatar = res.data.data.lowUrl;   
          }
          //console.log(that.total)
          //that.total=res.        
        }).catch(function(err) {
          console.log(err)
        })
      }
    },
    getRelatedData() {
      if (this.$route.params.no != '' || this.$route.params.no !== undefined) {
        var a = this.commonfunc._getRandom()
        var nonce = parseInt(a)
        var token = this.commonfunc._gtokene(nonce)
        var that = this
        this.no = this.$route.params.no
        var that = this
        this.axios.post(this.relateUrl, this.qs.stringify({ no: this.no, page: this.page }), {
          headers: { 'Content-Type': 'application/x-www-form-urlencoded', 'nonce': nonce, 'token': token }
        }).then(function(res) {
          if (parseInt(res.data.message) == 0) {
            that.pageList = res.data.data
            that.total = res.data.count
          }
          //console.log(that.total)
          //that.total=res.        
        }).catch(function(err) {
          console.log(err)
        })
      }
    },
    getViewData() {
      if (this.$route.params.no != '' || this.$route.params.no !== undefined) {
        var a = this.commonfunc._getRandom()
        var nonce = parseInt(a)
        var token = this.commonfunc._gtokene(nonce)
        var that = this
        this.no = this.$route.params.no
        var that = this
        this.axios.post(this.viewUrl, this.qs.stringify({ no: this.no}), {
          headers: { 'Content-Type': 'application/x-www-form-urlencoded', 'nonce': nonce, 'token': token }
        }).then(function(res) {      
        }).catch(function(err) {
        })
      }
    },
    getTopData() {
      if (this.$route.params.no != '' || this.$route.params.no !== undefined) {
        var a = this.commonfunc._getRandom()
        var nonce = parseInt(a)
        var token = this.commonfunc._gtokene(nonce)
        var that = this
        this.no = this.$route.params.no
        var that = this
        this.axios.post(this.topUrl, this.qs.stringify({ no: this.no}), {
          headers: { 'Content-Type': 'application/x-www-form-urlencoded', 'nonce': nonce, 'token': token }
        }).then(function(res) {
          if (parseInt(res.data.message) == 0) {
            that.topPageList = res.data.data
          }   

        }).catch(function(err) {
        })
      }
    },

    search() {
      this.is_search = true;
      this.cur_page = 1;
      this.getData();
    },
    formatter(row, column) {
      return row.address;
    },
    filterTag(value, row) {
      return row.tag === value;
    },
    strLen(str) {
      var len = 0;
      for (var i = 0; i < str.length; i++) {
        var c = str.charCodeAt(i);
        //单字节加1 
        if ((c >= 0x0001 && c <= 0x007e) || (0xff60 <= c && c <= 0xff9f)) {
          len++;
        } else {
          len += 2;
        }
      }
      return len;
    },
  }
}

</script>
<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
h1,
h2 {
  font-weight: normal;
}

ul {
  list-style-type: none;
  padding: 0;
}

li {
  display: inline-block;
  margin: 0 10px;
}

a {
  color: #42b983;
}

.pActive {
  color: blue;
}

</style>
<style>
.gli_slideIn {
  height: 287.403px;
}

.gli_slideOut {
  height: 272.767px;
}

</style>



